@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot?qxee29");
  src: url("../fonts/icomoon.eot?qxee29#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?qxee29") format("truetype"), url("../fonts/icomoon.woff?qxee29") format("woff"), url("../fonts/icomoon.svg?qxee29#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@media screen and (min-width: 768px) {
  [class^="icon-"]::before,
  [class*=" icon-"]::before {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    font-size: 0.75rem;
    margin-right: 10px;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

@media screen and (max-width: 768px) {
  [class^="icon-"]::before,
  [class*=" icon-"]::before {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    font-size: 1rem;
    margin-right: 5px;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

